
import APIs from '@/APIs'
import Tools from '@/APIs/Tools'
import { ElMessage } from 'element-plus'

import Mocker from '@/APIs/MockerAPIs/Mocker'
import { Options, Vue } from 'vue-class-component'

import UserInfoPanel from '@/components/UserInfoPanel.vue'

@Options({
  name: 'App',
  components: {
    'user-info-panel': UserInfoPanel
  },
  data () {
    return {
      display_login_info: false,
      userName: 'UserName',
      userAvatar: '',
      userPostNum: 0,
      userReplyNum: 0,
      userHasNewMessage: false,
      userInfoLoading: false,
      userTags: [],
      searchFilter: ''
    }
  },
  created () {
    this.$watch(
      () => this.$route.path,
      () => {
        this.loadMembersPosts()
      }
    )
  },
  mounted () {
    console.log(process.env.BASE_URL)
    const query = Tools.locationSearchToQuery(window.location.search)
    this.searchFilter = query.search || ''
    this.loadMembersPosts()
  },
  methods: {
    handleCommand (cmd:string) {
      this.onSignInButtonPressed(cmd)
    },
    onSignInButtonPressed (userName:string) {
      // Test Mocker APIs
      Mocker.loginTestUser(userName)
      const r = {
        path: this.$route.path,
        query: { ...this.$route.query, login: 'yes' },
        hash: this.$route.hash,
        params: { ...this.$route.params }
      }
      this.$router.push(r)
      setTimeout(() => {
        this.$router.go(-1)
        this.loadMembersPosts()

        APIs.checkToken().then((vaule) => {
          const v = vaule as { userName:string, userAvatar:string }
          this.display_login_info = true
          this.userName = v.userName
          this.userAvatar = v.userAvatar
        }).catch((v) => {
          this.display_login_info = false
          ElMessage.error('Check token fail. ' + v)
        })
      }, 1000)
    },
    onLogoutClicked () {
      Tools.setLoginTokenCookie('')
      this.display_login_info = false
      const r = {
        path: this.$route.path,
        query: { ...this.$route.query, login: 'no' },
        hash: this.$route.hash,
        params: { ...this.$route.params }
      }
      this.$router.push(r)
      setTimeout(() => {
        this.$router.go(-1)
        this.loadMembersPosts()
      }, 1000)
    },
    onUserInfoPanelShowed () {
      this.userInfoLoading = true
      APIs.getUserInfo().then((value) => {
        const v = value as { userPostNum:number, userReplyNum:number, userTags:unknown }
        this.userPostNum = v.userPostNum
        this.userReplyNum = v.userReplyNum
        this.userTags = v.userTags
      }).catch((e) => {
        ElMessage.error('Can\'t get user info.' + e)
      }).then(() => {
        this.userInfoLoading = false
      })
    },
    loadMembersPosts () {
      this.$store.dispatch('updateMembersPosts')
    },
    onSearchButtonClicked () {
      const r = {
        path: this.$route.path,
        query: { ...this.$route.query, search: this.searchFilter, page: 1 },
        hash: this.$route.hash,
        params: { ...this.$route.params }
      }
      this.$router.push(r)
    }
  }
})
export default class App extends Vue {}
